* {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box; 
    box-sizing: border-box;
}

html {
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
}

body{
    font-family: Verdana;
    font-size: 16px;
}

header {
    background-color: $primaryColor;
    padding: 1rem 2%;
}

header h1 {
    margin: 0;
}

header h2 {
    margin: 0;

}

nav {
    background-color: #111;
}

nav ul {
    margin: 0;
    padding: 0;
}

nav li{
    list-style-type: none;
}
nav li .active {
    background-color: #333;
}
nav a {
    display: block;
    padding: .8rem 0;
    color: #eee;
    text-align: center;
    text-decoration: none;
}

nav a:hover {
    background-color: #444;
}



main {
    padding: 1rem 2%;
    display: block;
}

main h1 {
    color: $primaryColor;
}

main figure img {
    width: 100%;
}

main figure {
    width: 100%;
    margin: 0;
}

main figcaption {
     text-align: center;
     font-size: .8rem;
}


footer {
    text-align: center;
    padding: 1rem 2%;
    background-color: $primaryColor;
}





.clearfix:after {
    content: "";
    display: table;
    clear: both;
}